* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  background-color: #0f1424;
  font-family: Rubik, system-ui;
}

.container {
  flex-direction: column;
  align-items: center;
  gap: 2.2rem;
  width: 88%;
  margin: 2rem auto;
  display: flex;
}

.menu-card {
  background-color: #1c1f4a;
  border-radius: 1.6rem;
  width: 100%;
}

.menu-card .card-report {
  background-color: #5847eb;
  border-radius: 1.6rem;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 1.8rem;
  padding: 3rem;
  display: flex;
}

.menu-card .card-report img {
  border: 3px solid #fff;
  border-radius: 50%;
  width: 26%;
  max-width: 130px;
}

.menu-card .card-report .report {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: .4rem;
  display: flex;
}

.menu-card .card-report .report p {
  color: #bdc1ff;
  font-size: 1.5rem;
}

.menu-card .card-report .report .name {
  color: #fff;
  font-size: 2.35rem;
  font-weight: 300;
}

.menu-card .menu {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 2.5rem;
  display: flex;
}

.menu-card .menu .menu-link {
  color: #6f76c8;
  cursor: pointer;
  font-size: 1.8rem;
}

.menu-card .menu .menu-link:hover, .menu-card .menu .menu-active {
  color: #fff;
}

.regular-card {
  background-position: 92% -1rem;
  background-repeat: no-repeat;
  border-radius: 1.6rem;
  width: 100%;
}

.regular-card .property-card {
  background-color: #1c1f4a;
  border-radius: 1.6rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: .8rem;
  margin-top: 4rem;
  padding: 2.5rem;
  display: flex;
}

.regular-card .property-card:hover {
  background-color: #33387a;
}

.regular-card .property-card .row {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.regular-card .property-card .row .title {
  color: #fff;
  font-size: 1.8rem;
  font-weight: 500;
}

.regular-card .property-card .row .points {
  justify-content: center;
  align-items: center;
  display: flex;
}

.regular-card .property-card .row .points:hover .point {
  background-color: #fff;
}

.regular-card .property-card .row .points .point {
  background-color: #bdc1ff;
  border-radius: 50%;
  width: .4rem;
  height: .4rem;
  margin: .2rem;
}

.regular-card .property-card .row-2 {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.regular-card .property-card .row-2 .hours {
  color: #fff;
  font-size: 3rem;
  font-weight: 300;
}

.regular-card .property-card .row-2 .description {
  color: #bdc1ff;
  font-size: 1.5rem;
}

.work {
  background-color: #ff8c66;
  background-image: url("icon-work.6a44be42.svg");
}

.play {
  background-color: #56c2e6;
  background-image: url("icon-play.a0c17ac8.svg");
}

.study {
  background-color: #ff5c7c;
  background-image: url("icon-study.4dc5924d.svg");
}

.exercise {
  background-color: #4acf81;
  background-image: url("icon-exercise.9cd631a0.svg");
}

.social {
  background-color: #7536d3;
  background-image: url("icon-social.4885365b.svg");
}

.self-care {
  background-color: #f1c65b;
  background-image: url("icon-self-care.07368a06.svg");
}

@media screen and (width >= 570px) {
  .container {
    grid-template-columns: 48% 48%;
    display: grid;
  }

  .menu-card {
    grid-column: 1 / span 2;
  }

  .menu-card .card-report {
    gap: 3rem;
  }

  .work-card .property-card .row-2 {
    flex-direction: column;
    align-items: flex-start;
  }

  .work-card .property-card .row-2 .hours {
    font-size: 3rem;
  }
}

@media screen and (width >= 1440px) {
  body {
    justify-content: center;
    align-items: center;
    height: 100vh;
    display: flex;
  }

  .container {
    grid-template-columns: repeat(4, 23%);
    gap: 3rem;
    width: 77%;
  }

  .menu-card {
    grid-row: 1 / span 2;
    grid-column: unset;
    height: 100%;
  }

  .menu-card .card-report {
    flex-direction: column;
    align-items: flex-start;
    height: 70%;
    padding: 3.5rem 3rem;
  }

  .menu-card .card-report img {
    width: 8.4rem;
  }

  .menu-card .card-report .report {
    margin-top: 1rem;
  }

  .menu-card .card-report .report .name {
    font-size: 4rem;
  }

  .menu-card .menu {
    flex-direction: column;
    align-items: flex-start;
    height: 30%;
  }

  .regular-card .property-card {
    margin-top: 4.5rem;
    padding: 2.8rem;
  }

  .regular-card .property-card .row-2 .hours {
    padding-top: 1.9rem;
    font-size: 5.5rem;
  }

  .regular-card .property-card .row-2 .description {
    padding-top: 1rem;
  }
}
