@media screen and (min-width: 1440px) {
  body {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }

  .container {
    width: 77%;
    grid-template-columns: repeat(4, 23%);
    gap: 3rem;
  }

  .menu-card {
    grid-row: 1 / span 2;
    grid-column: unset;
    height: 100%;

    .card-report {
      flex-direction: column;
      align-items: flex-start;
      height: 70%;
      padding: 3.5rem 3rem;

      img {
        width: 8.4rem;
      }

      .report {
        margin-top: 1rem;

        .name {
          font-size: 4rem;
        }
      }
    }
    .menu {
      height: 30%;
      flex-direction: column;
      align-items: flex-start;
    }
  }

  .regular-card {
    .property-card {
      margin-top: 4.5rem;
      padding: 2.8rem;
      .row-2 {
        .hours {
          font-size: 5.5rem;
          padding-top: 1.9rem;
        }

        .description {
          padding-top: 1rem;
        }
      }
    }
  }
}
